.graph{
    grid-area: graph;
    display: grid;
    grid-template-columns: 1fr 200px 150px;
    grid-template-areas: 'title dates checkbox'
                            'graph graph graph';


    border-radius: $rounded;
    padding: 25px;
    align-items: center;
    grid-gap: 25px;
    background-color: #FFF;
    border: 1px solid $light-grey;
    &__dates{
        align-self: center;
        grid-area: dates;
        display: flex;
        margin-right: 30px;
    }
    &__dates-dash{
        margin: 0 5px;
    }
    &__date{
        border-radius: $rounded;
        padding: 0px 5px;
    }

    &__checkbox{
        grid-area: checkbox;
    }

    &__image{
        grid-area: graph;
        width: 100%;
    }
}