.main{
    padding: 10px 50px 50px;
    grid-area: main;
    border-radius: 0 0 $rounded 0;
    display: grid;
    grid-template-columns: repeat(3, minmax(290, 1fr));
    grid-template-rows: repeat(3, 1fr);
    grid-template-areas: 'cards cards cards'
                        'graph graph diagram'
                        'histogram histogram location';
    grid-gap: 32px;
}