.cards{
    grid-area: cards;
    display: grid;
    grid-template-columns: repeat(3, 340px);
    grid-gap: 32px;
}

.card{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 'title btn'
                            'info statistic';

    padding: 25px;
    border: 1px solid $light-grey;
    border-radius: $rounded;
    border: 1px solid $light-grey;
    background-color: #FFF;

    &__menu{
        align-self: start;
        background: inherit;
        border: none;
        cursor: pointer;
    }

    &__statistic-text{
        margin-right: 10px;
    }
}