.header{
    grid-area: header;
    display: grid;
    grid-template-columns: 150px 250px 1fr;
    grid-template-areas: "title search account";
    align-items: center;
    padding: 20px 50px;
    border-radius: 0 $rounded 0 0; 
    &__title{
        grid-area: title;
    }

    &__search-form{
        grid-area: search;
        align-self: center;
    }
    
    &__search{
        height: 30px;
        padding: 10px 10px;
        border-radius: $rounded;
        border: 1px solid $light-grey;
        margin-right: 30px;
    }

    &__search::placeholder{
        color: $light-grey;
    }

    &__notifications{
        background: none;
        border: none;
        cursor: pointer;
    }
    &__account{
        grid-area: account;
        display: grid;
        grid-template-areas: 'account-name photo btn' ;
        grid-gap: 10px;
        justify-self: end;
        align-items: center;
    }

    &__account-photo{
        background: white;
        border-radius: 35px;
        cursor: pointer;
    }
    &__dropdown-btn{
        background-color: inherit;
        border: none;
        cursor: pointer;
    }

}