.nav{
    padding: 50px 0px;
    grid-area: nav;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: $rounded 0 0 $rounded;
    background-color: grey ;

    &__logo{
        width: 40px;
        height: 25px;
    }
    &__item{
        padding: 5px;
        margin-bottom: 15px;
    }

    &__link{
        cursor: pointer;
    }

    &__mode-btn{
        background-color: inherit;
        border: none;
        cursor: pointer;
    }
}