body {
    background-color: $primary-bg;
}

.app{
    display: grid;
    background-color: $app-bg;
    grid-template-columns: 100px 1fr;
    grid-template-rows: 120px 1fr;
    grid-template-areas: 
                        'nav header'
                        'nav main';
    max-width: 1280px;
    min-height: 880px;
    margin: 50px auto;
    border: 1px solid black;
    border-radius: $rounded;
    overflow: hidden;
}
