.diagram{
    padding: 25px;
    grid-area: diagram;
    display: grid;
    grid-template-areas: 'title title'
                            'img features';
    border-radius: $rounded;
    background-color: #FFF;
    border: 1px solid $light-grey;
    &__image{
        grid-area: img;
    }

    &__graph-name-container{
        grid-area: features;
    }

    &__graph-name{
        margin-bottom: 10px;
    }

}