.histogram{
    padding: 25px;
    grid-area: histogram;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 'title checkbox'
                            'graph graph';
    border-radius: $rounded;
    align-items: center;
    background-color: #FFF;
    border: 1px solid $light-grey;


    &__graph-name-container{
        grid-area: checkbox;
        display: grid;
        grid-template-areas: 'women men';
        grid-gap: 20px;
    }

    &__title{
        margin-bottom: 20px;
    }

    &____graph-name{
        display: flex;
    }
    &__graph-name__text{
        align-self: center;
    }

    &__graph-name__sign{
        align-self: center;
    }

    &__image{
        grid-area: graph;
        width: 100%;
    }
}