.location{
    padding: 25px;
    grid-area: location;
    display: grid;
    grid-template-areas: 'title btns'
                            'graph graph';
    
    border-radius: $rounded;
    background-color: #FFF;
    border: 1px solid $light-grey;

    &__title{
        grid-area: title;
    }

    &__select{
        grid-area: btns;
    }

    &__image{
        grid-area: graph;
    }

    &__select{
        justify-self: end;
    }
    
    &__btn--active{
        background: inherit;
        border: none;
        cursor: pointer;
    }

    &__btn--passive{
        background: inherit;
        border: none;
        cursor: pointer;
    }

}